////// Packages //////////////////
import React, { FC, useEffect, useState } from 'react'
import { FormGroup, Row, Col, Label, InputGroupAddon, InputGroup, Input, UncontrolledTooltip } from 'reactstrap'
////// Local Components //////////

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';
import ButtonIconTooltip from 'components/ButtonIconTooltip'
import ButtonIcon from 'components/ButtonIcon'
import { IAddress } from 'store/types/models/id';
////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props {
    id: string;
    address?: IAddress;
    onChange: (address: IAddress)=>void;
}

const AddressForm: FC<Props> = ({ id, address, onChange }) => {

    if (!address) return null

    const setTitle = (title: string) => onChange({...address!, title})
    const setStreet = (street: string[]) => onChange({...address!, street})
    const setCity = (city: string) => onChange({...address!, city})
    const setState = (state: string) => onChange({...address!, state})
    const setZipCode = (zipCode: string) => onChange({...address!, zipCode})

    const { title, street, city, state, zipCode } = address || {}

    return (
        <FormGroup className="btn-reveal-trigger">
            <div className="p-2 border">
                <FormGroupInput
                    id={`address-title-${id}`}
                    label='Title'
                    placeholder="Title..."
                    value={title}
                    onChange={e => setTitle(e.target.value)}
                />
                <h6>Street Address</h6>
                <div className="pl-2 mb-2 border border-top-0 border-bottom-0 border-right-0">
                    {street.map((line, i) => (
                        <FormGroup key={i}>
                            <Label>{'Line ' + (i + 1)}</Label>
                            <InputGroup>
                                <Input
                                    id={`address-line-${i}-${id}`}
                                    label={'Line ' + (i + 1)}
                                    placeholder="..."
                                    value={line}
                                    onChange={e => {
                                        const newStreet = [...street]
                                        newStreet[i] = e.target.value
                                        
                                        setStreet(newStreet)
                                    }}
                                />
                                <InputGroupAddon addonType="append">
                                    <ButtonIconTooltip
                                        id={`delete-${id}-${i}`}
                                        icon="trash"
                                        color="danger"
                                        onClick={() => setStreet(street.filter((_, indx) => indx !== i))}
                                    >
                                        Delete Line
                                    </ButtonIconTooltip>
                                </InputGroupAddon>
                            </InputGroup>
                        </FormGroup>
                    ))}
                    <ButtonIcon
                        size="sm"
                        icon="plus"
                        color="falcon-primary"
                        onClick={() => setStreet([...street, ''])}
                    >
                        Add Line
                    </ButtonIcon>
                </div>

                <FormGroupInput
                    id={`address-city-${id}`}
                    label='City'
                    placeholder="City..."
                    value={city}
                    onChange={e => setCity(e.target.value)}
                />
                <FormGroupInput
                    id={`address-state-${id}`}
                    label='State'
                    placeholder="State..."
                    value={state}
                    onChange={e => setState(e.target.value)}
                />
                <FormGroupInput
                    id={`address-zipcode-${id}`}
                    label='Zip Code'
                    placeholder="Zip Code..."
                    value={zipCode}
                    onChange={e => setZipCode(e.target.value)}
                />
            </div>
        </FormGroup>
    )
}

export default AddressForm